import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { ElementDTO } from "../../dto/ElementDTO";
import { NodeDTO } from "../../dto/NodeDTO";
import { TextDTO } from "../../dto/TextDTO";
import { TreeDTO } from "../../dto/TreeDTO";

export enum NodeManipulation
{
  Append = 0,
  Prepend = 1,
  Before = 2,
  After = 3
}

export type TreeDTOAnswer = {
  ok: boolean,
  treeDTO: TreeDTO | null
}

export type NodeDTOAnswer = {
  ok: boolean,
  nodeDTO: ElementDTO | TextDTO | NodeDTO | null
}

export type NodeWithTreeDTOAnswer = TreeDTOAnswer & NodeDTOAnswer;


export type PageRequest = {
  pageName: string;
}

export type NodeRequest = PageRequest & {
  nodeID: string
}

export type UpdateClassNameRequest = NodeRequest & {
  className: string
}

export type UpdateStyleIDRequest = NodeRequest & {
  styleID: string
}

export type UpdateTagNameRequest = NodeRequest & {
  tagName: string
}

export type UpdateAttributesRequest = NodeRequest & {
  attributes: {
    [name: string]: string
  }
}

export type UpdateTextContentRequest = NodeRequest & {
  textContent: string
}

export type TransferNodeRequest = NodeRequest & {
  targetNodeID: string,
  nodeManipulation: NodeManipulation
}

export type CreateTextNodeRequest = NodeRequest & {
  nodeManipulation: NodeManipulation
}

export type CreateElmNodeRequest = NodeRequest & {
  targetNodeTag: string,
  nodeManipulation: NodeManipulation
}


export const pageApi = createApi({
	reducerPath: "pageApi",
	baseQuery: fetchBaseQuery({ baseUrl: "http://localhost:5000/api/Page" }),
	endpoints: (build) => ({
    getTree: build.query<TreeDTOAnswer, string> ({
      query: (pageName: string) => ({
				url: "/GetTree",
        params: {
          pageName
        },
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    getNodeInfo: build.query<NodeDTOAnswer, NodeRequest> ({
      query: (nodeReq: NodeRequest) => ({
        url: "/GetNodeInfo",
        params: nodeReq,
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    updateClassName: build.mutation<NodeDTOAnswer, UpdateClassNameRequest>({
      query: (updateReq: UpdateClassNameRequest) => ({
        url: "/UpdateClassName",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: updateReq
      })
    }),

    updateStyleID: build.mutation<NodeDTOAnswer, UpdateStyleIDRequest>({
      query: (updateReq: UpdateStyleIDRequest) => ({
        url: "/UpdateID",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: updateReq
      })
    }),

    updateAttributes: build.mutation<NodeDTOAnswer, UpdateAttributesRequest>({
      query: (updateReq: UpdateAttributesRequest) => ({
        url: "/UpdateAttributes",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: updateReq
      })
    }),

    updateTextContent: build.mutation<NodeDTOAnswer, UpdateTextContentRequest>({
      query: (updateReq: UpdateTextContentRequest) => ({
        url: "/UpdateTextContent",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: updateReq
      })
    }),

    updateTagName: build.mutation<NodeDTOAnswer, UpdateTagNameRequest>({
      query: (updateReq: UpdateTagNameRequest) => ({
        url: "/UpdateTagName",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: updateReq
      })
    }),

    deleteNode: build.mutation<TreeDTOAnswer, NodeRequest>({
      query: (req: NodeRequest) => ({
        url: "/DeleteNode",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),

    transferNode: build.mutation<TreeDTOAnswer, TransferNodeRequest>({
      query: (req: TransferNodeRequest) => ({
        url: "/TransferNode",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),

    createTextNode: build.mutation<NodeWithTreeDTOAnswer, CreateTextNodeRequest>({
      query: (req: CreateTextNodeRequest) => ({
        url: "/CreateTextNode",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),

    createElmNode: build.mutation<NodeWithTreeDTOAnswer, CreateElmNodeRequest>({
      query: (req: CreateTextNodeRequest) => ({
        url: "/CreateElmNode",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),
	}),
});

export const {
  useLazyGetNodeInfoQuery,
  useLazyGetTreeQuery,
  useUpdateClassNameMutation,
  useUpdateAttributesMutation,
  useUpdateStyleIDMutation,
  useUpdateTextContentMutation,
  useUpdateTagNameMutation,
  useDeleteNodeMutation,
  useTransferNodeMutation,
  useCreateElmNodeMutation,
  useCreateTextNodeMutation
} = pageApi;
