import { useEffect, useRef, useState } from 'react';
import { useLazySaveProjectQuery } from '../../../../redux/api/project.api';
import { ProjectRouteEnum, projectSlice } from '../../../../redux/slices/projectSlice';
import { Tool, toolSlice, ToolType } from '../../../../redux/slices/toolSlice';
import { useAppDispatch, useAppSelector } from '../../../../redux/store';
import { Icon } from '../../../../resources/Icons';
import './ToolPanel.scss';

export function ToolPanel(): JSX.Element {
  const markupTools = useAppSelector(state => state.toolSliceReducer.markupTools);
  const styleTools = useAppSelector(state => state.toolSliceReducer.styleTools);
  const curTool = useAppSelector(state => state.toolSliceReducer.curTool);
  const projectRoute = useAppSelector(state => state.projectSliceReducer.projectRoute);
  const toolPanelRef = useRef<HTMLDivElement>(null);

  const dispath = useAppDispatch();

  const [selectedTool, setSelectedTool] = useState<Tool | null>(curTool);

  const [saveProject] = useLazySaveProjectQuery();

  // const clearSelectedTool = (e: MouseEvent) => {
    
    // console.log([ToolType.TextWrapper, ToolType.OtherWrapper, ToolType.SectionWrapper].includes(selectedTool?.id ?? ToolType.Click))
    // var path = (e.composedPath && e.composedPath()) as HTMLElement[];

    // if ([ToolType.TextWrapper, ToolType.OtherWrapper, ToolType.SectionWrapper].includes(selectedTool?.id ?? ToolType.TextWrapper)) {
    //   setSelectedTool(curTool);
    // }

    // if (path && toolPanelRef.current && path.find(elm => elm === toolPanelRef.current as HTMLElement)
    //   && ![ToolType.TextWrapper, ToolType.OtherWrapper, ToolType.SectionWrapper].includes(selectedTool?.id ?? ToolType.Click)) {
      
    // } else {
    //   setSelectedTool(curTool);
    // }
  // };

  useEffect(() => {
    if (curTool.wrapper) {
      setSelectedTool(null);
    } else {
      setSelectedTool(curTool);
    }
  }, [curTool]);

  const setCurTool = (tool: Tool) => {
    if (tool.wrapper) {
      setSelectedTool(selectedTool === tool ? curTool : tool);
    } else {
      dispath(toolSlice.actions.setCurTool(tool));
    }
  }

  const setStylePageRoute = () => {
    dispath(projectSlice.actions.setProjectRoute(ProjectRouteEnum.StylesPage))
  }

  const setMarkupPageRoute = () => {
    dispath(projectSlice.actions.setProjectRoute(ProjectRouteEnum.MarkupPage));
  }

  const openFontPop = () => {
    dispath(projectSlice.actions.setFontPopOpened(true));
  }

  useEffect(() => {
    if (projectRoute === ProjectRouteEnum.MarkupPage) {
      dispath(toolSlice.actions.setCurTool(markupTools[0]))
    } else if (projectRoute === ProjectRouteEnum.StylesPage) {
      dispath(toolSlice.actions.setCurTool(styleTools[0]))
    }
  }, [projectRoute])

  // useEffect(() => {
  //   document.addEventListener("click", clearSelectedTool);

  //   return () => {
  //     document.removeEventListener("click", clearSelectedTool);
  //   }
  // }, []);

  

  return (
    <div className="tool-panel" ref={toolPanelRef}>
      <div className='tool-panel__tools'>
        {
          projectRoute === ProjectRouteEnum.MarkupPage && markupTools.map(tool => (
            <button 
              className={`tool-panel__tools-item ${
                tool === curTool || tool === selectedTool || tool.innerTools.find(innerTool => innerTool === curTool)
                  ? "tool-panel__tools-item_selected" 
                  : ""}`
              } 
              key={tool.id}
              onClick={() => setCurTool(tool)}
            >
              {tool.imgPath && <img className="tool-panel__tools-item-pic" src={tool.imgPath} alt=""/> }
              {tool.dropIcon && <span className='tool-panel__tools-item-drop'></span>}
            </button>
          ))
        }
        {
          projectRoute === ProjectRouteEnum.MarkupPage && markupTools.map((tool, toolIndex) => (
            tool.wrapper &&
            <div 
              className={`tool-panel__tools-sub-list tool-panel__tools-sub-list_${toolIndex} ${
                tool === selectedTool 
                  ? "tool-panel__tools-sub-list_opened" 
                  : ""}`
              } 
              key={tool.id}
            >
              {
                tool.innerTools.map(tool => (
                  <button 
                    className={`tool-panel__tools-sub-item ${
                      tool === curTool
                        ? "tool-panel__tools-sub-item_selected" 
                        : ""}`
                    } 
                    key={tool.id}
                    onClick={() => setCurTool(tool)}
                  >
                    {tool.imgPath && <img className="tool-panel__tools-sub-item-pic" src={tool.imgPath} alt=""/> }
                    <div className='tool-pane__tools-sub-item-text'>{tool.description}</div>
                  </button>
                ))
              }
            </div>
          ))
        }

        {
          projectRoute === ProjectRouteEnum.StylesPage && styleTools.map(tool => (
            <button 
              className={`tool-panel__tools-item ${
                tool === curTool || tool === selectedTool || tool.innerTools.find(innerTool => innerTool === curTool)
                  ? "tool-panel__tools-item_selected" 
                  : ""}`
              } 
              key={tool.id}
              onClick={() => setCurTool(tool)}
            >
              {tool.imgPath && <img className="tool-panel__tools-item-pic" src={tool.imgPath} alt=""/> }
              {tool.dropIcon && <span className='tool-panel__tools-item-drop'></span>}
            </button>
          ))
        }
      </div>

      <div className='tool-panel__project'>
        <button className='tool-panel__project-btn'  title="Шрифты" onClick={openFontPop}>
          <img src={Icon.FontWhite} alt="" />
        </button>

        {
          projectRoute === ProjectRouteEnum.MarkupPage ?
          <button className='tool-panel__project-btn' onClick={() => setStylePageRoute()} title="Стилизация">
            <img src={Icon.CSSWhite} alt="" />
          </button> :
          <button className='tool-panel__project-btn tool-panel__project-btn_active' onClick={() => setMarkupPageRoute()} title="Разметка">
            <img src={Icon.CSSWhite} alt="" />
          </button>
        }

        

        <button className='tool-panel__project-btn' onClick={() => saveProject()} title="Сохранить">
          <img src={Icon.SaveWhite} alt="" />
        </button>

        <a className='tool-panel__project-btn' href="http://localhost:5000/api/Project/ExportProject" download="download" title="Экспорт">
          <img src={Icon.ExportWhite} alt="" />
        </a>
      </div>
    </div>
  )
}