import { useEffect, useState } from "react";
import { ElementDTO } from "../../../../../../dto/ElementDTO";
import { Icon } from "../../../../../../resources/Icons";
import './TagSettings.scss';

const headerTextTags = ["h1", "h2", "h3", "h4", "h5", "h6"];
const textTags = ["p", "span", "a", "div"];
const sectionTags = ["div", "section", "header", "footer"];


function findTagVarsForTagsName(tagName: string) {
  switch (tagName) {
    case "div":
    case "section":
    case "header":
    case "footer":
      return sectionTags;

    case "a":
    case "p":
    case "span":
      return textTags;

    case "h1":
    case "h2":
    case "h3":
    case "h4":
    case "h5":
    case "h6":
      return headerTextTags;

    default:
      return [tagName];
  }
}

export function TagSettings({node, tagName, setTagName}: {node: ElementDTO, tagName: string, setTagName: React.Dispatch<string>}): JSX.Element {
  const [tagVars, setTagVars] = useState<string[]>([]);
  const [opened, setOpened] = useState(false);


  const toggleOpen = () => {
    setOpened(!opened);
  }

  const changeTagName = (tagName: string) => {
    setTagName(tagName);
    setOpened(false);
  }
  
  useEffect(() => {
    setOpened(false);
    setTagName(node.tagName);
    setTagVars(findTagVarsForTagsName(node.tagName));
  }, [node]);
  
  return (
    <div className="tag-settings">
      <button className="tag-settings__open-btn" onClick={toggleOpen}>
        <span>{tagName}</span>

        <img src={Icon.ArrowDropIconWhite} alt=""/>
      </button>

      <div className={`tag-settings__drop-menu ${opened ? "tag-settings__drop-menu_opened" : ""}`}>
        {
          tagVars.map((tag) => (
            <button className={`tag-settings__drop-menu-item ${tag === tagName ? "tag-settings__drop-menu-item_active" : ""}`}
              onClick={() => changeTagName(tag)}
              key={tag}>
              <span>{tag}</span>
            </button>
          ))
        }
      </div>
    </div>
  )
}