import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ElementDTO } from "../../dto/ElementDTO";
import { NodeDTO } from "../../dto/NodeDTO";
import { TextDTO } from "../../dto/TextDTO";


export interface INodeTreeSlice {
  rootNode: ElementDTO | null;
  allNodes: {
    [name: string]: ElementDTO | TextDTO | NodeDTO
  } | null
}

const initialState: INodeTreeSlice = {
  rootNode: null,
  allNodes: null
};

export const nodeTreeSlice = createSlice({
    initialState,
    name: 'nodeTreeSlice',
    reducers: {
      updateTree(state: INodeTreeSlice, action: PayloadAction<INodeTreeSlice>) {
        state.rootNode = action.payload.rootNode;
        state.allNodes = action.payload.allNodes;
      },

      clearTree(state: INodeTreeSlice) {
        state.rootNode = null;
        state.allNodes = null;
      }
    }
});
