import { useEffect } from "react"
import { StyleComponentDTO } from "../../../../../dto/StyleComponentDTO";
import './SelectorSettings.scss';

export function SelectorSettings({styleComponent, selector, setSelector}: {styleComponent: StyleComponentDTO, selector: string, setSelector: React.Dispatch<string>}) :JSX.Element {
  useEffect(() => {
    setSelector(styleComponent.selector);
  }, [styleComponent]);
  
  return (
    <div className="selector-settings">
      <textarea value={selector} onInput={e => setSelector((e.target as HTMLTextAreaElement).value)} placeholder="selector"/>
    </div>
  )
}