import { useEffect, useState } from "react";
import { ElementDTO } from "../../../../../../dto/ElementDTO";
import "./AttributeSettings.scss";

export function AttributeSettings({node, attributes, setAttributes}: {node: ElementDTO, attributes: string[][], setAttributes: React.Dispatch<React.SetStateAction<string[][]>>}): JSX.Element {
  useEffect(() => {
    const newAttibutes: string[][] = [];

    for (let attributeKey of Object.keys(node.attributes)) {
      newAttibutes.push([attributeKey, node.attributes[attributeKey]]);
    }

    setAttributes(newAttibutes.filter(attribute => {
      return attribute.length === 2 && attribute[0].length > 0 && attribute[1].length > 0;
    }));
  }, [node]);


  const addAttribute = () => {
    setAttributes([...attributes, ["", ""]]);
  };

  const changeAttribute = (attributeIndex: number, fieldIndex: number, text: string) => {
    const newAttibutes = [...attributes];
    newAttibutes[attributeIndex][fieldIndex] = text;
    setAttributes(newAttibutes);
  }

  return (
    <div className="attribute-settings">
      {
        attributes.map((attribute, attributeIndex) => (
          <div className="attribute-settings__item" key={attributeIndex}>
            <input 
              type="text" 
              value={attribute[0]} 
              onInput={e => changeAttribute(attributeIndex, 0, (e.target as HTMLInputElement).value)}
              placeholder="key"
            />
            <span>:</span>
            <input 
              type="text" 
              value={attribute[1]} 
              onInput={e => changeAttribute(attributeIndex, 1, (e.target as HTMLInputElement).value)}
              placeholder="value"
            />
          </div>
        ))
      }

      {(attributes.length === 0 
        || (attributes[attributes.length - 1][0].length !== 0 &&  attributes[attributes.length - 1][1].length !== 0))
        && <button className="attribute-settings__add-btn" onClick={addAttribute}>Добавить</button>}
    </div>
  )
}