import { useEffect, useState } from "react";
import { useRemoveStyleComponentMutation, useUpdateStyleComponentMutation } from "../../../../redux/api/style.api";
import { useAppDispatch, useAppSelector } from "../../../../redux/store"
import { styleSlice } from './../../../../redux/slices/styleSlice';
import { styleTreeSlice } from './../../../../redux/slices/styleTreeSlice';
import { Icon } from "../../../../resources/Icons";
import { SelectorSettings } from "./SelectorSettings/SelectorSettings";
import './StyleComponentSettings.scss';
import { StyleSettings } from "./StyleSettings/StyleSettings";
import { StyleComponentDTO } from "../../../../dto/StyleComponentDTO";

export function StyleComponentSettings() : JSX.Element {
  const allStyleComponents = useAppSelector(state => state.styleTreeSliceReducer.allStyleComponents);
  const curStyleComponent = useAppSelector(state => state.styleSliceReducer.curStyleComponent);
  const curStyleSheet = useAppSelector(state => state.projectSliceReducer.activeStyleSheet);

  const [curOpenedSection, setCurOpenedSection] = useState(-1);

  const [selector, setSelector] = useState("");
  const [styles, setStyles] = useState<string[][]>([]);

  const [updateStyleComponentMutation] = useUpdateStyleComponentMutation();
  const [removeStyleComponentMutation] = useRemoveStyleComponentMutation();

  
  const dispatch = useAppDispatch();


  const updateStyleComponent = () => {
    const endStyles: {[name: string]: string} = {};

    styles.forEach(stylePair => {
      if (stylePair.length === 2 && stylePair[0].length > 0 && stylePair[1].length > 0) endStyles[stylePair[0]] = stylePair[1];
    });

    updateStyleComponentMutation({sheetName: curStyleSheet, styleID: curStyleComponent!.styleID, selector, styles: endStyles})
      .then(res => {
        if ("data" in res) {
          if (res.data.ok) {
            const newStyleComponents = allStyleComponents!.map(styleComponent => {
              if (styleComponent.styleID !== curStyleComponent!.styleID) return styleComponent;
              return res.data.styleComponentDTO as StyleComponentDTO;
            });

            dispatch(styleTreeSlice.actions.updateTree({allStyleComponents: newStyleComponents}));
            dispatch(styleSlice.actions.clearCurNode());
          } else {
            alert("Ты делаешь что-то не так");
          }
        } else {
          console.log(res.error);
          alert("Перезагрази сервак");
        }
      })
      .catch(err => {
        console.log(err);
        alert("Перезагрази сервак");
      })
  }

  const removeStyleComponent = () => {
    removeStyleComponentMutation({sheetName: curStyleSheet, styleID: curStyleComponent!.styleID})
      .then(res => {
        if ("data" in res) {
          if (res.data.ok) {
            dispatch(styleTreeSlice.actions.updateTree({allStyleComponents: res.data.styleSheetDTO!.styleComponents}));
            dispatch(styleSlice.actions.clearCurNode());
          } else {
            alert("Ты делаешь что-то не так");
          }
        } else {
          console.log(res.error);
          alert("Перезагрази сервак");
        }
      })
      .catch(err => {
        console.log(err);
        alert("Перезагрази сервак");
      });
  }

  useEffect(() => {
    setCurOpenedSection(-1);
  }, [curStyleComponent, curStyleSheet])

  return (
    <div className="style-component-settings">
      {
        curStyleComponent && (
          <>
            <div className={`node-settings__section ${curOpenedSection === 0 ? "node-settings__section_opened" : ""}`}>
              <button className="node-settings__section-open-btn" onClick={curOpenedSection === 0 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(0)}>
                <span>Селектор</span>
                <img src={Icon.ArrowDropIconWhite} alt=""/>
              </button>

              <div className="node-settings__section-settings">
                <SelectorSettings styleComponent={curStyleComponent} selector={selector} setSelector={setSelector}/>
              </div>
            </div>

            <div className={`node-settings__section ${curOpenedSection === 1 ? "node-settings__section_opened" : ""}`}>
              <button className="node-settings__section-open-btn" onClick={curOpenedSection === 1 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(1)}>
                <span>Стили</span>
                <img src={Icon.ArrowDropIconWhite} alt=""/>
              </button>

              <div className="node-settings__section-settings">
                <StyleSettings styleComponent={curStyleComponent} styles={styles} setStyles={setStyles}/>
              </div>
            </div>

            {/* <div className={`node-settings__section ${curOpenedSection === 1 ? "node-settings__section_opened" : ""}`}>
              <button className="node-settings__section-open-btn" onClick={curOpenedSection === 1 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(1)}>
                <span>Макет</span>
                <img src={Icon.ArrowDropIconWhite} alt=""/>
              </button>

              <div className="node-settings__section-settings"> */}
                {/* <AttributeSettings node={node} attributes={attributes} setAttributes={setAttributes}/> */}
              {/* </div>
            </div> */}

            {/* <div className={`node-settings__section ${curOpenedSection === 2 ? "node-settings__section_opened" : ""}`}>
              <button className="node-settings__section-open-btn" onClick={curOpenedSection === 2 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(2)}>
                <span>Текст</span>
                <img src={Icon.ArrowDropIconWhite} alt=""/>
              </button>

              <div className="node-settings__section-settings"> */}
                {/* <AttributeSettings node={node} attributes={attributes} setAttributes={setAttributes}/> */}
              {/* </div>
            </div> */}

            {/* <div className={`node-settings__section ${curOpenedSection === 3 ? "node-settings__section_opened" : ""}`}>
              <button className="node-settings__section-open-btn" onClick={curOpenedSection === 3 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(3)}>
                <span>Оформление</span>
                <img src={Icon.ArrowDropIconWhite} alt=""/>
              </button>

              <div className="node-settings__section-settings"> */}
                {/* <AttributeSettings node={node} attributes={attributes} setAttributes={setAttributes}/> */}
              {/* </div>
            </div> */}

            <div className="node-settings__buttons-cont">
              <button 
                className="node-settings__confirm-btn" 
                onClick={updateStyleComponent}
              >
                Применить
              </button>
              <button 
                className="node-settings__delete-btn" 
                onClick={removeStyleComponent}
              >
                Удалить правило
              </button>
            </div>
          </>
        )
      }
    </div>
  )
}