import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ElementDTO } from "../../dto/ElementDTO";
import { NodeDTO } from "../../dto/NodeDTO";
import { TextDTO } from "../../dto/TextDTO";


export interface INodeSlice {
  curNode: ElementDTO |  TextDTO | NodeDTO | null;
}

const initialState: INodeSlice = {
  curNode: null,
};

export const nodeSlice = createSlice({
    initialState,
    name: 'nodeSlice',
    reducers: {
      updateCurNode(state: INodeSlice, action: PayloadAction<ElementDTO |  TextDTO | NodeDTO>) {
        state.curNode = action.payload;
      },

      clearCurNode(state: INodeSlice) {
        state.curNode = null;
      }
    }
});
