import { NodeDTO } from "./NodeDTO"
import { TextDTO } from "./TextDTO";


export type ElementDTO = NodeDTO & {
  classList: string[],
  styleID: string,
  tagName: string,
  attributes: {[name: string]: string},
};

export function isElementDTO(node: NodeDTO | ElementDTO | TextDTO): node is ElementDTO {
  return (node as ElementDTO).tagName !== undefined;
}