import "./App.scss";
import Header from "./components/Header/Header";
import { ChoosePage } from "./components/pages/ChoosePage/ChoosePage";
import { CreatePage } from "./components/pages/CreatePage/CreatePage";
import { ProjectPage } from "./components/pages/ProjectPage/ProjectPage";
import { RouteEnum } from "./redux/slices/routeSlice";
import { useAppSelector } from "./redux/store";
// import Main from "./Main/Main";

function App() {
	const activePage: RouteEnum = useAppSelector(state => state.routeSliceReducer.route);

	return (
		<div className="wrapper">
			<Header />
			{
				activePage === RouteEnum.ChoosePage 
					? <ChoosePage/> : activePage === RouteEnum.CreatePage
					? <CreatePage/> : activePage === RouteEnum.ProjectPage 
					? <ProjectPage/> : null
			}
			{/* <Main /> */}
		</div>
	);
}

export default App;

