import { useEffect, useState, Fragment } from 'react';
import { ElementDTO, isElementDTO } from '../../../../dto/ElementDTO';
import { NodeDTO } from '../../../../dto/NodeDTO';
import { isTextDTO, TextDTO } from '../../../../dto/TextDTO';
import { NodeManipulation, NodeWithTreeDTOAnswer, useCreateElmNodeMutation, useCreateTextNodeMutation } from '../../../../redux/api/page.api';
import { nodeSlice } from '../../../../redux/slices/nodeSlice';
import { ToolType } from '../../../../redux/slices/toolSlice';
import { nodeTreeSlice } from '../../../../redux/slices/nodeTreeSlice';
import { useAppDispatch, useAppSelector } from '../../../../redux/store';
import { Icon } from '../../../../resources/Icons';
import './NodeTree.scss';

function findElementTag(tool: ToolType): string {
  switch (tool) {
    case ToolType.Link:
      return "a";
    case ToolType.TextHeader:
      return "h1";
    case ToolType.Paragraph:
      return "p";

    case ToolType.Section:
      return "div";
    case ToolType.Article:
      return "a";
    case ToolType.Iframe:
      return "iframe";

    case ToolType.Img:
      return "img";
    case ToolType. Audio:
      return "audio";
    case ToolType.Video:
      return "video";
    case ToolType.Canvas:
      return "canvas";
  
    default:
      return "";
  }
}

function findIconForElement(tagName: string): string {
  switch (tagName) {
    case "body":
    case "div":
    case "section":
      return Icon.SectionWhite;

    case "article":
      return Icon.ArticleWhite;

    case "audio":
      return Icon.AudioWhite;

    case "img":
      return Icon.ImgWhite;

    case "canvas":
      return Icon.CanvasWhite;

    case "iframe":
      return Icon.IframeWhite;

    case "a":
      return Icon.LinkWhite;

    case "p":
      return Icon.ParagraphWhite;

    case "h1":
    case "h2":
    case "h3":
    case "h4":
    case "h5":
    case "h6":
      return Icon.TextHeaderWhite;

    case "video":
      return Icon.VideoWhite;
    
    default:
      return "";
  }
}

export function NodeTree(): JSX.Element {
  const [openedNodes, setOpenedNodes] = useState<Set<string>>(new Set());

  const curPage = useAppSelector(state => state.projectSliceReducer.activePage);
  const rootNode = useAppSelector(state => state.nodeTreeSliceReducer.rootNode);
  const allNodes = useAppSelector(state => state.nodeTreeSliceReducer.allNodes);
	const curNode = useAppSelector((state) => state.nodeSliceReducer.curNode);

  const curTool = useAppSelector(state => state.toolSliceReducer.curTool);

  const [requestCreateTextNode, textNodeAnswerResult] = useCreateTextNodeMutation();
  const [requestCreateElmNode, elmNodeAnswerResult] = useCreateElmNodeMutation();

  const dispath = useAppDispatch();

  const openNode = (nodeID: string) => {
    if (allNodes) {
      dispath(nodeSlice.actions.updateCurNode(allNodes[nodeID]))
    } else {
      alert("Что-то пошло не так");
    }
  }

  const toggleTree = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, nodeID: string) => {
    e.stopPropagation();

    if (openedNodes.has(nodeID)) {
      openedNodes.delete(nodeID);
    } else {
      openedNodes.add(nodeID);
    }

    const newSet = new Set(openedNodes)

    setOpenedNodes(newSet);
  }

  const addButtonHandler = (nodeID: string, nodeManipulation: NodeManipulation) => {
    if (curTool.id === ToolType.Text) {
      requestCreateTextNode({
        pageName: curPage,
        nodeID: nodeID,
        nodeManipulation: nodeManipulation
      });
    } else {
      requestCreateElmNode({
        pageName: curPage,
        nodeID: nodeID,
        targetNodeTag: findElementTag(curTool.id),
        nodeManipulation: nodeManipulation
      })
    }
  }

  const renderAddButton = (nodeID: string, nodeManipulation: NodeManipulation) => {
    return (
      <li className='node-tree__item' key={`append-${nodeID}`}>
        <div className="node-tree__item-add-btn" onClick={() => addButtonHandler(nodeID, nodeManipulation)}>
          <span>+</span>
        </div>
      </li>
    );
  }

  const renderCont = (props: JSX.Element[], nodeID: string, activeAdd: boolean, rootNode: boolean) => (
    <ul className='node-tree__cont'>
      {props}
      {(activeAdd && !rootNode) && renderAddButton(nodeID, NodeManipulation.Append)}
    </ul>
  )

  const renderNode = (nodeID: string, activeAdd: boolean, rootNode: boolean) => {
    const node: ElementDTO | TextDTO | NodeDTO | null = allNodes ? allNodes[nodeID] : allNodes;
    if (node === null) return <></>;
    const imgSrc = isTextDTO(node) 
      ? Icon.TextWhite : isElementDTO(node)
      ? findIconForElement(node.tagName) :
      Icon.SectionWhite;

		return (
      <Fragment key={`fragment-${nodeID}`}>
        {(activeAdd && !rootNode) && renderAddButton(nodeID, NodeManipulation.Before)}
        <li className={`node-tree__item ${openedNodes.has(nodeID) ? "node-tree__item_opened" : ""}`} key={nodeID}>
          <div 
            className={`node-tree__item-btn ${curNode && curNode.nodeID === node.nodeID ? "node-tree__item-btn_active" : ""}`}
            onClick={() => openNode(node.nodeID)}
          > 
            {
              !isTextDTO(node) &&
              <button className={`node-tree__item-show-tree-btn`} onClick={e => toggleTree(e, nodeID)}></button>
            }
            {
              imgSrc &&
              <div className='node-tree__item-pic'>
                <img src={imgSrc} alt=""/>
              </div>
            }
            
            <div className="node-tree__item-text">
              {
                isElementDTO(node) 
                  ? `${node.tagName}#${node.styleID}` : isTextDTO(node) 
                  ? `text#${node.textContent.slice(0, 10)}` : `${node.nodeID}`
              }
            </div>
          </div>
          
          {!isTextDTO(node) && renderCont(node.childNodes.map((child) => renderNode(child, activeAdd, false)), nodeID, activeAdd, false)}
        </li>
        
      </Fragment>
		);
	};

  const handleCreateNode = (data: NodeWithTreeDTOAnswer) => {
    if (data.ok) {
      dispath(nodeTreeSlice.actions.updateTree(data.treeDTO!));
      dispath(nodeSlice.actions.updateCurNode(data.nodeDTO!));
    } else {
      alert("Ты делаешь что-то не так");
    }
  }

  useEffect(() => {
    if (textNodeAnswerResult.isError) alert("Перезагрузи сервак");
    if (textNodeAnswerResult.isSuccess) handleCreateNode(textNodeAnswerResult.data);
    textNodeAnswerResult.isSuccess = false;
  }, [textNodeAnswerResult]);

  useEffect(() => {
    if (elmNodeAnswerResult.isError) alert("Перезагрузи сервак");
    if (elmNodeAnswerResult.isSuccess) handleCreateNode(elmNodeAnswerResult.data);
    elmNodeAnswerResult.isSuccess = false;
  }, [elmNodeAnswerResult]);

  return (
    <div className='node-tree'>
      {rootNode && renderCont([renderNode(rootNode.nodeID, curTool.id !== ToolType.Click, true)], rootNode.nodeID, curTool.id !== ToolType.Click, true)}
    </div>
  )
}