import { useEffect, useRef } from "react";
import { useAppSelector } from "../../../../redux/store";
import "./RenderArea.scss";

export function RenderArea() : JSX.Element {
  const pageName = useAppSelector(state => state.projectSliceReducer.activePage);
  const pageRef = useRef<HTMLIFrameElement>(null);

  const pageTree = useAppSelector(state => state.nodeTreeSliceReducer.allNodes);

  const reloadPage = () => {
    if (pageRef.current !== null) pageRef.current.src = `http://localhost:5000/Develop/${pageName}`;
  };

  useEffect(() => {
    reloadPage();
  }, [pageTree, pageName])

  return (
    <div className="render-area">
      <div className="render-area__reload-btn-cont">
        <button className="render-area__reload-btn" onClick={reloadPage}>Перезагрузить страницу</button>
      </div>
      <div className="render-area__cont">
        <iframe src={`http://localhost:5000/Develop/${pageName}`} title="Отрисовка страницы" ref={pageRef}/>
      </div>
    </div>
  )
}