import { useEffect } from "react"
import { ElementDTO } from "../../../../../../dto/ElementDTO";
import './StyleIDSettings.scss';

export function StyleIDSettings({node, styleID, setStyleID}: {node: ElementDTO, styleID: string, setStyleID: React.Dispatch<string>}) :JSX.Element {
  useEffect(() => {
    setStyleID(node.styleID);
  }, [node]);
  
  return (
    <div className="style-id-settings">
      <input type="text" value={styleID} onInput={e => setStyleID((e.target as HTMLInputElement).value)} placeholder="ID"/>
    </div>
  )
}