import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { StyleComponentDTO } from "../../dto/StyleComponentDTO";

export interface IStyleTreeSlice {
  allStyleComponents: StyleComponentDTO[] | null
}

const initialState: IStyleTreeSlice = {
  allStyleComponents: null
};

export const styleTreeSlice = createSlice({
    initialState,
    name: 'styleTreeSlice',
    reducers: {
      updateTree(state: IStyleTreeSlice, action: PayloadAction<IStyleTreeSlice>) {
        state.allStyleComponents = action.payload.allStyleComponents;
      },

      clearTree(state: IStyleTreeSlice) {
        state.allStyleComponents = null;
      }
    }
});
