import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export enum RouteEnum {
  ChoosePage,
  CreatePage,
  ProjectPage
}

export interface IRouteSlice {
  route: RouteEnum;
}

const initialState: IRouteSlice = {
  route: RouteEnum.ChoosePage
};

export const routeSlice = createSlice({
    initialState,
    name: 'routeSlice',
    reducers: {
        setActivePage(state: IRouteSlice, action: PayloadAction<RouteEnum>) {
            state.route = action.payload;
        }
    }
});
