import { useEffect, useState } from "react";
import { StyleComponentDTO } from "../../../../../dto/StyleComponentDTO";
import "./StyleSettings.scss";

export function StyleSettings({styleComponent, styles, setStyles}: {styleComponent: StyleComponentDTO, styles: string[][], setStyles: React.Dispatch<React.SetStateAction<string[][]>>}): JSX.Element {
  useEffect(() => {
    const newStyles: string[][] = [];

    for (let styleKey of Object.keys(styleComponent.styles)) {
      newStyles.push([styleKey, styleComponent.styles[styleKey]]);
    }

    setStyles(newStyles.filter(style => {
      return style.length === 2 && style[0].length > 0 && style[1].length > 0;
    }));
  }, [styleComponent]);

  const addStyle = () => {
    setStyles([...styles, ["", ""]]);
  };

  const changeStyle = (styleIndex: number, fieldIndex: number, text: string) => {
    const newStyles = [...styles];
    newStyles[styleIndex][fieldIndex] = text;
    setStyles(newStyles);
  }

  return (
    <div className="style-settings">
      {
        styles.map((style, styleIndex) => (
          <div className="style-settings__item" key={styleIndex}>
            <input 
              type="text" 
              value={style[0]} 
              onInput={e => changeStyle(styleIndex, 0, (e.target as HTMLInputElement).value)}
              placeholder="key"
            />
            <span>:</span>
            <input 
              type="text" 
              value={style[1]} 
              onInput={e => changeStyle(styleIndex, 1, (e.target as HTMLInputElement).value)}
              placeholder="value"
            />
          </div>
        ))
      }

      {(styles.length === 0 
        || (styles[styles.length - 1][0].length !== 0 &&  styles[styles.length - 1][1].length !== 0))
        && <button className="style-settings__add-btn" onClick={addStyle}>Добавить</button>}
    </div>
  )
}