import { useEffect, useRef } from "react";
import { useCreateProjectMutation, useLazyLoadProjectQuery } from "../../../redux/api/project.api";
import { projectSlice } from "../../../redux/slices/projectSlice";
import { RouteEnum, routeSlice } from "../../../redux/slices/routeSlice";
import { useAppDispatch } from "../../../redux/store";
import "./CreatePage.scss";


export function CreatePage(): JSX.Element {
  const dispatch = useAppDispatch();
  const input = useRef<HTMLInputElement>(null);
  const [createProjectQuery, result] = useCreateProjectMutation();
  const [loadProject] = useLazyLoadProjectQuery();


	const createProject = () => {
    createProjectQuery(input.current?.value || "/")
	}

  useEffect(() => {
    if (result.isError) {
      alert("Перезагрузи сервак");
    }

    if (result.isSuccess) {
      if (result.data.ok) {
        loadProject(result.data.projectName ?? "")
          .then(res => {
            if (res.isSuccess) {
              if (res.data.projectDTO !== null) {
                dispatch(projectSlice.actions.setProject(res.data.projectDTO))
                dispatch(routeSlice.actions.setActivePage(RouteEnum.ProjectPage));
              }
              
            } else {
              alert("Перезагрузи сервак");
            }
          })
      } else {
        alert(result.data.error);
      }
    }
    result.isSuccess = false;
  }, [result]);


  return (
    <div className="create-page">
			<div className="create-page__cont">
        <div className="create-page__name-input-cont">
          <label className="create-page__name-input-label" htmlFor="project-name-input">Имя проекта:</label>
          <input className="create-page__name-input" type="text" placeholder="Введите" id="project-name-input" ref={input}/>
        </div>
        <button className="create-page__create-btn" onClick={() => createProject()}>
          Создать
        </button>
			</div>
    </div>
  )
}

