import { useEffect, useRef, useState } from "react";
import { ElementDTO } from "../../../../../dto/ElementDTO";
import { NodeDTO } from "../../../../../dto/NodeDTO";
import { TreeDTO } from "../../../../../dto/TreeDTO";

import { useDeleteNodeMutation, useLazyGetNodeInfoQuery, useUpdateAttributesMutation, useUpdateClassNameMutation, useUpdateStyleIDMutation, useUpdateTagNameMutation } from "../../../../../redux/api/page.api";
import { useLazyGetTreeQuery } from "../../../../../redux/api/page.api";
import { nodeSlice } from "../../../../../redux/slices/nodeSlice";
import { nodeTreeSlice } from "../../../../../redux/slices/nodeTreeSlice";
import { useAppDispatch } from "../../../../../redux/store";
import { Icon } from "../../../../../resources/Icons";
import { AttributeSettings } from "./AttributesSettings/AttributeSettings";
import { ClassNameSettings } from "./ClassNameSettings/ClassNameSettings";
import { StyleIDSettings } from "./StyleIDSettings/StyleIDSettings";
import { TagSettings } from "./TagSettings/TagSettings";

export function ElementSettings({pageName, node}: {pageName: string, node: ElementDTO}): JSX.Element {
  const [curOpenedSection, setCurOpenedSection] = useState(-1);

  const [attributes, setAttributes] = useState<string[][]>([]);
  const [classList, setClassList] = useState<string[]>([]);
  const [styleID, setStyleID] = useState<string>("");
  const [tagName, setTagName] = useState<string>("");

  const [updateClassName] = useUpdateClassNameMutation();
  const [updateStyleID] = useUpdateStyleIDMutation();
  const [updateAttributes] = useUpdateAttributesMutation();
  const [updateTagName] = useUpdateTagNameMutation();

  const [deleteNode] = useDeleteNodeMutation()

  const [getTree] = useLazyGetTreeQuery();
  const [getNode] = useLazyGetNodeInfoQuery();

  const dispatch = useAppDispatch();


  useEffect(() => {
    setCurOpenedSection(-1);
  }, [pageName, node]);

  const updateAllSettings = () => {
    const endClassName = classList.join(" ");
    const endStyleID = styleID;
    const endAttributes = getAttributes();
    const endTagName = tagName;

    Promise.all([
      new Promise((res) => {updateClassName({pageName: pageName, nodeID: node.nodeID, className: endClassName}).then(() => res(true));}),
      new Promise((res) => {updateStyleID({pageName: pageName, nodeID: node.nodeID, styleID: endStyleID}).then(() => res(true))}),
      new Promise((res) => {updateAttributes({pageName: pageName, nodeID: node.nodeID, attributes: endAttributes}).then(() => res(true))}),
      new Promise((res) => {updateTagName({pageName: pageName, nodeID: node.nodeID, tagName: endTagName}).then(() => res(true))}),
    ])
    .then(() => {
      getTree(pageName)
        .then(res => {
          if (res.isSuccess) {
            if (res.data.ok) {
              const treeDTO = (res.data.treeDTO as TreeDTO);
              dispatch(nodeTreeSlice.actions.updateTree(treeDTO));
              updateNodeInfo();
            } else {
              alert("Ты что-то не то делаешь");
            }
          } else {
            alert("Перезагрузи сервер");
          }
        })
    })
  };

  const updateNodeInfo = () => {
    getNode({pageName: pageName, nodeID: node.nodeID})
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            const nodeDTO = res.data.nodeDTO;
            dispatch(nodeSlice.actions.updateCurNode(nodeDTO!));
          } else {
            dispatch(nodeSlice.actions.clearCurNode());
          }
        } else {
          alert("Перезагрузи сервер");
        }
      })
  };

  const deleteElement = () => {
    deleteNode({pageName: pageName, nodeID: node.nodeID})
      .then(res => {
        if ("data" in res) {
          dispatch(nodeSlice.actions.clearCurNode())
          dispatch(nodeTreeSlice.actions.updateTree(res.data.treeDTO!));
        }
      });
  };

  const getAttributes = () => {
    const newAttributes: {[name: string]: string} = {};
    for (let i = 0; i < attributes.length; i++) {
      const attribute = attributes[i];
      if (attribute.length !== 2 || attribute[0].length === 0 || attribute[1].length === 0) continue;
      newAttributes[attribute[0]] = attribute[1];
    }
    return newAttributes;
  }


  return (
    <>

      <div className={`node-settings__section ${curOpenedSection === 0 ? "node-settings__section_opened" : ""}`}>
        <button className="node-settings__section-open-btn" onClick={curOpenedSection === 0 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(0)}>
          <span>Атрибуты</span>
          <img src={Icon.ArrowDropIconWhite} alt=""/>
        </button>

        <div className="node-settings__section-settings">
          <AttributeSettings node={node} attributes={attributes} setAttributes={setAttributes}/>
        </div>
      </div>

      <div className={`node-settings__section ${curOpenedSection === 1 ? "node-settings__section_opened" : ""}`}>
        <button className="node-settings__section-open-btn" onClick={curOpenedSection === 1 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(1)}>
          <span>Классы</span>
          <img src={Icon.ArrowDropIconWhite} alt=""/>
        </button>

        <div className="node-settings__section-settings">
          <ClassNameSettings node={node} classList={classList} setClassList={setClassList}/>
        </div>
      </div>

      <div className={`node-settings__section ${curOpenedSection === 2 ? "node-settings__section_opened" : ""}`}>
        <button className="node-settings__section-open-btn" onClick={curOpenedSection === 2 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(2)}>
          <span>ID</span>
          <img src={Icon.ArrowDropIconWhite} alt=""/>
        </button>

        <div className="node-settings__section-settings">
          <StyleIDSettings node={node} styleID={styleID} setStyleID={setStyleID}/>
        </div>
      </div>

      <div className={`node-settings__section ${curOpenedSection === 3 ? "node-settings__section_opened" : ""}`}>
        <button className="node-settings__section-open-btn" onClick={curOpenedSection === 3 ? () => setCurOpenedSection(-1) : () => setCurOpenedSection(3)}>
          <span>Тег</span>
          <img src={Icon.ArrowDropIconWhite} alt=""/>
        </button>

        <div className="node-settings__section-settings">
          <TagSettings node={node} tagName={tagName} setTagName={setTagName}/>
        </div>
      </div>

      <div className="node-settings__buttons-cont">
        <button className="node-settings__confirm-btn" onClick={updateAllSettings}>Применить</button>
        {node.tagName !== "body" && <button className="node-settings__delete-btn" onClick={deleteElement}>Удалить элемент</button>}
      </div>
    </>
  )
}