import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { Icon } from "../../resources/Icons";

export enum ToolType {
  Click,

  TextWrapper,
  Text,
  Link,
  TextHeader,
  Paragraph,

  SectionWrapper,
  Section,
  Article,
  Iframe,

  Hand,

  Dots,

  OtherWrapper,
  Img,
  Audio,
  Video,
  Canvas,

  AddStyleRule
}

export type Tool = {
  id: ToolType;
  imgPath: string;
  description: string;
  wrapper: boolean,
  dropIcon: boolean,
  // parentTool: ToolType | null; 
  innerTools: Tool[];
}


export interface IToolSlice {
  styleTools: Tool[],
  markupTools: Tool[],
  curTool: Tool
}

