import { RouteEnum, routeSlice } from '../../../redux/slices/routeSlice';
import { useAppDispatch, useAppSelector } from '../../../redux/store';
import { Icon } from '../../../resources/Icons';
import './Bar.scss';

interface BarProps {
    activeRoute: number
}

export default function Bar({activeRoute} : BarProps): JSX.Element {
    const dispatch = useAppDispatch();
    const goToChoosePage = () => {
        dispatch(routeSlice.actions.setActivePage(RouteEnum.ChoosePage))
    }

    return (
        <div className="bar">
            <div className='bar__logo-cont'>
                <div className="bar__logo">
                    <img src={Icon.Logo} alt=""/>
                </div>
            </div>

            <div className='bar__center-cont'>
                {
                    activeRoute === RouteEnum.ChoosePage
                        ? <div>Выбор проекта</div> : activeRoute === RouteEnum.CreatePage
                        ? <div>Создание проекта</div> : activeRoute === RouteEnum.ProjectPage
                        ? <div>Вёрстка</div> : null

                }
            </div>
                
            {/* <div className="bar__project dropdown">
                <span>Project <img src="img/arrow.svg" /></span>
                <ul>
                    <li><a href="#">Project Cat</a></li>
                    <li><a href="#">Project Dog</a></li>
                    <li><a href="#">Project Bird</a></li>
                </ul>
            </div> */}
            {/* <Dropdown pagesCount={3} title={'Project'} pages={['cat', 'dog', 'bird']} /> */}
            <div className='bar__exit-btn-cont'>
                <button className='bar__exit-btn' onClick={goToChoosePage}></button>
            </div>
        </div>
    );
};
