import { useEffect } from 'react';
import { useLazyGetProjectsNamesQuery, useLazyLoadProjectQuery } from '../../../redux/api/project.api';
import { RouteEnum, routeSlice } from '../../../redux/slices/routeSlice';
import { projectSlice } from "../../../redux/slices/projectSlice";
import { useAppDispatch } from '../../../redux/store';
import './ChoosePage.scss';


export function ChoosePage(): JSX.Element {
  const [getProjectList, {data: projectList}] = useLazyGetProjectsNamesQuery();
  const [getProject] = useLazyLoadProjectQuery();
  const dispatch = useAppDispatch();

  const openProject = (projectName: string) => {
    getProject(projectName)
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            if (res.data.projectDTO !== null) {
              dispatch(projectSlice.actions.setProject(res.data.projectDTO))
              dispatch(routeSlice.actions.setActivePage(RouteEnum.ProjectPage));
            }
          } else {
            alert(res.data.error);
          }
        } else {
          alert("Перезагрузи сервак");
        }
      });
  }

	const openCreatePage = () => dispatch(routeSlice.actions.setActivePage(RouteEnum.CreatePage));

  useEffect(() => {
    getProjectList();
  }, []);

  return (
    <div className="choose-page">

      <div className="choose-page__new-btn-cont">
        <button className="choose-page__new-btn" onClick={openCreatePage}>
          <span>Создать новый проект</span>
          <span></span>
        </button>
      </div>

      <h3 className="choose-page__header">Ваши проекты:</h3>

      <ul className="choose-page__project-list">
        {
          projectList && projectList.map((project, projectIndex) => 
            <li className="choose-page__project-list-item" key={projectIndex}>
              <button className="choose-page__project-list-item-btn" onClick={() => openProject(project)}>
                <span className="choose-page__project-list-item-point">P{projectIndex + 1}</span>
                <span className="choose-page__project-list-item-name">{project}</span>
              </button>
            </li>
          )
        }
      </ul>
    </div>
  )
}