import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { StyleComponentDTO } from "../../dto/StyleComponentDTO";

export interface INodeSlice {
  curStyleComponent: StyleComponentDTO | null;
}

const initialState: INodeSlice = {
  curStyleComponent: null,
};

export const styleSlice = createSlice({
    initialState,
    name: 'styleSlice',
    reducers: {
      updateCurNode(state: INodeSlice, action: PayloadAction<StyleComponentDTO>) {
        state.curStyleComponent = action.payload;
      },

      clearCurNode(state: INodeSlice) {
        state.curStyleComponent = null;
      }
    }
});
