import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { StyleComponentDTO } from "../../dto/StyleComponentDTO";
import { StyleSheetDTO } from "../../dto/StyleSheetDTO";

export enum StyleManipulation
{
  DocumentStart = 0,
  AfterComponent = 1
}

export type StyleSheetAnswer = {
  ok: boolean,
  styleSheetDTO: StyleSheetDTO | null
}

export type StyleComponentAnswer = {
  ok: boolean,
  styleComponentDTO: StyleComponentDTO | null
}

export type StyleSheetWithComponentAnswer = StyleSheetAnswer & {
  styleComponentDTO: StyleComponentDTO | null
}


export type SheetRequest = {
  sheetName: string
}

export type ComponentRequest = SheetRequest & {
  styleID: string
}

export type CreateComponentRequest = ComponentRequest & {
  styleManipulation: StyleManipulation
}

export type UpdateComponentRequest = ComponentRequest & {
  selector: string,
  styles: {[name: string]: string}
}


export const styleApi = createApi({
	reducerPath: "styleApi",
	baseQuery: fetchBaseQuery({ baseUrl: "http://localhost:5000/api/StyleSheet" }),
	endpoints: (build) => ({
    getStyleSheet: build.query<StyleSheetAnswer, SheetRequest> ({
      query: (req) => ({
				url: "/GetStyleSheet",
        params: req,
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    createStyleComponent: build.mutation<StyleSheetWithComponentAnswer, CreateComponentRequest>({
      query: (req) => ({
        url: "/CreateStyleComponent",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),

    updateStyleComponent: build.mutation<StyleComponentAnswer, UpdateComponentRequest>({
      query: (req) => ({
        url: "/UpdateComponentStyles",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    }),

    removeStyleComponent: build.mutation<StyleSheetAnswer, ComponentRequest>({
      query: (req) => ({
        url: "/RemoveComponentStyles",
        method: "POST",
				cache: "no-cache",
				responseHandler: "json",
        body: req
      })
    })
	}),
});

export const {
  useLazyGetStyleSheetQuery,
  useCreateStyleComponentMutation,
  useUpdateStyleComponentMutation,
  useRemoveStyleComponentMutation
} = styleApi;
