import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ProjectDTO } from "../api/project.api";

export enum ProjectRouteEnum {
  MarkupPage,
  StylesPage
}

export interface IProjectSlice {
  projectRoute: ProjectRouteEnum,
  fontPopOpened: boolean,
  name: string,
  activePage: string,
  pagePaths: string[],
  activeStyleSheet: string,
  styleSheetPaths: string[],
  connectedFonts: string[],
  fonts: string[]
}

const initialState: IProjectSlice = {
  projectRoute: ProjectRouteEnum.MarkupPage,
  fontPopOpened: false,
  name: "",
  activePage: "",
  pagePaths: [],
  activeStyleSheet: "",
  styleSheetPaths: [],
  connectedFonts: [],
  fonts: []
}

export const projectSlice = createSlice({
  initialState,
  name: 'projectSlice',
  reducers: {
    setProject(state: IProjectSlice, action: PayloadAction<IProjectSlice | ProjectDTO>) {
      state.projectRoute = ProjectRouteEnum.MarkupPage;
      state.name = action.payload.name;
      state.activePage = action.payload.pagePaths[0] || "";
      state.pagePaths = action.payload.pagePaths;
      state.activeStyleSheet = action.payload.styleSheetPaths[0] || "";
      state.styleSheetPaths = action.payload.styleSheetPaths;
      state.connectedFonts = [];
      state.fonts = action.payload.fonts;
    },

    clearProject(state: IProjectSlice) {
      state.name = "";
      state.pagePaths = [];
      state.styleSheetPaths = [];
      state.connectedFonts = [];
      state.fonts = [];
      state.fontPopOpened = false;
    },

    setActiveProjectPage(state: IProjectSlice, action: PayloadAction<string>) {
      state.activePage = action.payload;
    },

    setActiveStyleSheet(state: IProjectSlice, action: PayloadAction<string>) {
      state.activeStyleSheet = action.payload;
    },

    setProjectRoute(state: IProjectSlice, action: PayloadAction<ProjectRouteEnum>) {
      state.projectRoute = action.payload;
    },

    setFontPopOpened(state: IProjectSlice, action: PayloadAction<boolean>) {
      state.fontPopOpened = action.payload;
    },

    setConnectedFonts(state: IProjectSlice, action: PayloadAction<string[]>) {
      state.connectedFonts = action.payload;
    },

    setProjectFonts(state: IProjectSlice, action: PayloadAction<string[]>) {
      state.fonts = action.payload;
    }
  }
});