import { useEffect } from 'react';
import { ElementDTO } from '../../../../../../dto/ElementDTO';
import { Icon } from '../../../../../../resources/Icons';
import './ClassNameSettings.scss';

export function ClassNameSettings({node, classList, setClassList}: {node: ElementDTO, classList: string[], setClassList: React.Dispatch<React.SetStateAction<string[]>>} ): JSX.Element {
  
  useEffect(() => {
    setClassList(node.classList.filter(className => className.length > 0));
  }, [node]);
  
  const addClassName = () => {
    setClassList([...classList, ""]);
  };

  const deleteClassName = (classNameIndex: number) => {
    const newClassList = [...classList];
    newClassList.splice(classNameIndex, 1);
    setClassList(newClassList);
  };

  const changeClassName = (classNameIndex: number, text: string) => {
    const newClassList = [...classList];
    newClassList[classNameIndex] = text;
    setClassList(newClassList);
  };

  
  
  return (
    <div className='class-name-settings'>
      {
        classList.map((className, classNameIndex) => (
          <div className='class-name-settings__item' key={classNameIndex}>
            <input 
              type="text" 
              value={className} 
              onInput={e => changeClassName(classNameIndex, (e.target as HTMLInputElement).value)}
              placeholder="Class Token"
            />
            <button className='class-name-settings__item-remove-btn' onClick={() => deleteClassName(classNameIndex)}>
              <img src={Icon.MinusWhite} alt=""/>
            </button>
          </div>
        ))
      }

      {(classList.length === 0 || (classList[classList.length - 1].length !== 0))
        && 
        <button className="class-name-settings__add-btn" onClick={addClassName}>
          <span>Добавить</span>
          <img src={Icon.PlusWhite} alt=""/>
        </button>}
    </div>
  )
}