import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";



export type FontRequest =
{
  fontName: string
}

export type FontConnectRequest = FontRequest &
{
  pageName: string,
  connect: boolean
}

export type FontCreateRequest = FontRequest &
{
  fontFiles: File, 
  fontStyle: string,
  fontWeight: string
}


export type FontAPIResponse = {
  ok: boolean,
  error: string | null
}

export type FontListResponse = FontAPIResponse & {
  fonts: string[] | null
}

export type FontConnectedListResponse = FontListResponse & {
  connectedFonts: string[] | null
}

export type FontCreatedResponse = FontListResponse & {
  fontName: string | null
}


export const fontApi = createApi({
  reducerPath: "fontApi",
  baseQuery: fetchBaseQuery({ baseUrl: "http://localhost:5000/api/Font" }),
  endpoints: (build) => ({
    getProjectFonts: build.query<FontListResponse, void> ({
      query: () => ({
        url: "/GetProjectFonts",
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    getConnectedFonts: build.query<FontConnectedListResponse, string> ({
      query: (pageName) => ({
        url: "/GetConnectedFonts",
        params: {
          pageName: pageName
        },
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    connectFont: build.mutation<FontConnectedListResponse, FontConnectRequest> ({
      query: (req) => ({
        url: "/ConnectFont",
        body: req,
        method: "POST",
				cache: "no-cache",
				responseHandler: "json"
      })
    }),

    uploadFont: build.mutation<FontCreatedResponse, FontCreateRequest> ({
      query: (req) => {
        const formData = new FormData();
        formData.append("fontName", req.fontName);
        formData.append("fontStyle", req.fontStyle);
        formData.append("fontWeight", req.fontWeight);
        formData.append("fontFiles", req.fontFiles);

        return {
          url: "/UploadFont",
          body: formData,
          method: "POST",
          cache: "no-cache",
          responseHandler: "json",
          // headers: {
          //   "Content-Type": "multipart/form-data"
          // }
        };
      }
    })
  })
});


export const {
  useLazyGetConnectedFontsQuery,
  useLazyGetProjectFontsQuery,
  useConnectFontMutation,
  useUploadFontMutation
} = fontApi;
