import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export type ProjectDTO = {
	name: string,
	pagePaths: string[],
	styleSheetPaths: string[],
	fonts: string[]
}

export type LoadProjectDTO = {
	ok: boolean,
	projectDTO: ProjectDTO | null,
	error: string | null
}

export type CreateProjectDTO = {
  ok: boolean,
	projectName: string | null,
  error: string | null
}

export const projectApi = createApi({
	reducerPath: "projectApi",
	baseQuery: fetchBaseQuery({ baseUrl: "http://localhost:5000/api/Project" }),
	endpoints: (build) => ({
		getProjectsNames: build.query<string[], void>({
			query: () => ({
				url: "/GetProjectsNames",
				cache: "no-cache",
				responseHandler: "json"
			})
		}),

		loadProject: build.query<LoadProjectDTO, string>({
			query: (projectName: string) => ({
				url: "/LoadProject",
				cache: "no-cache",
				params: {
					name: projectName
				},
				responseHandler: "json"
			}),
		}),

		createProject: build.mutation<CreateProjectDTO, string> ({
			query: (projectName: string) => ({
				url: "/CreateProject",
				cache: "no-cache",
				method: "POST",
				params: {
					name: projectName
				},
				responseHandler: "json"
			}),

			// transformResponse: (response: { data: CreateProjectDTO }, meta, arg) => response.data,
			// transformErrorResponse: (resp)
		}),

		saveProject: build.query<any, void> ({
			query: () => ({
				url: "/SaveProject",
				cache: "no-cache",
				responseHandler: "json"
			})
		})
	}),
});

// export const { useLazyGetProjectListQuery } = projectListApi;
export const { useLazyGetProjectsNamesQuery, useLazyLoadProjectQuery, useLazySaveProjectQuery, useCreateProjectMutation } = projectApi;
