import { useEffect, useState } from 'react';
import { TextDTO } from '../../../../../dto/TextDTO';
import { TreeDTO } from '../../../../../dto/TreeDTO';
import { useDeleteNodeMutation, useLazyGetNodeInfoQuery, useLazyGetTreeQuery, useUpdateTextContentMutation } from '../../../../../redux/api/page.api';
import { nodeSlice } from '../../../../../redux/slices/nodeSlice';
import { nodeTreeSlice } from '../../../../../redux/slices/nodeTreeSlice';
import { useAppDispatch } from '../../../../../redux/store';

export function TextSettings({pageName, node}: {pageName: string, node: TextDTO}): JSX.Element {
  const [textContent, setTextContent] = useState<string>("");

  const [updateTextContentQuery] = useUpdateTextContentMutation();
  const [deleteNode] = useDeleteNodeMutation();

  const [getTree] = useLazyGetTreeQuery();
  const [getNode] = useLazyGetNodeInfoQuery();

  const dispatch = useAppDispatch();

  useEffect(() => {
    setTextContent(node.textContent);
  }, [node]);


  const updateTextContent = () => {
    updateTextContentQuery({pageName: pageName, nodeID: node.nodeID, textContent: textContent})
      .then(() => {
        getTree(pageName)
          .then(res => {
            if (res.isSuccess) {
              if (res.data.ok) {
                const treeDTO = (res.data.treeDTO as TreeDTO);
                dispatch(nodeTreeSlice.actions.updateTree(treeDTO));
                updateNodeInfo();
              } else {
                alert("Ты что-то не то делаешь");
              }
            } else {
              alert("Перезагрузи сервер");
            }
        })
      })
  }


  const updateNodeInfo = () => {
    getNode({pageName: pageName, nodeID: node.nodeID})
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            const nodeDTO = res.data.nodeDTO;
            dispatch(nodeSlice.actions.updateCurNode(nodeDTO!));
          } else {
            dispatch(nodeSlice.actions.clearCurNode());
          }
        } else {
          alert("Перезагрузи сервер");
        }
      })
  };

  const deleteText = () => {
    deleteNode({pageName: pageName, nodeID: node.nodeID})
      .then(res => {
        if ("data" in res) {
          dispatch(nodeSlice.actions.clearCurNode())
          dispatch(nodeTreeSlice.actions.updateTree(res.data.treeDTO!));
        }
      });
  };

  return (
    <>
      <div className='node-settings__text-cont'>
        <textarea value={textContent} onInput={e => setTextContent((e.target as HTMLTextAreaElement).value)}></textarea>
      </div>

      <div className="node-settings__buttons-cont">
        <button className="node-settings__confirm-btn" onClick={updateTextContent}>Применить</button>
        <button className="node-settings__delete-btn" onClick={deleteText}>Удалить текст</button>
      </div>
    </>
    
  )
}