import { useRef, useState } from 'react';
import Dropzone, { DropEvent, FileRejection } from 'react-dropzone';
import { useConnectFontMutation, useUploadFontMutation } from '../../../../redux/api/font.api';
import { projectSlice } from '../../../../redux/slices/projectSlice';
import { useAppDispatch, useAppSelector } from '../../../../redux/store';
import { Icon } from '../../../../resources/Icons';
import './FontPop.scss';


export function FontPop() : JSX.Element {
  const formRef = useRef<HTMLFormElement>(null);

  const [fontName, setFontName] = useState("");
  const [fontStyle, setFontStyle] = useState("");
  const [fontWeight, setFontWeight] = useState("");
  const [file, setFile] = useState<File | null>(null);

  const curPage = useAppSelector(state => state.projectSliceReducer.activePage);
  const fonts = useAppSelector(state => state.projectSliceReducer.fonts);
  const connectedFonts = useAppSelector(state => state.projectSliceReducer.connectedFonts);

  const [uploadFontMutation] = useUploadFontMutation();
  const [connectFontMutation] = useConnectFontMutation();

  // const {getRootProps, getInputProps, isDragActive}

  const dispatch = useAppDispatch();

  const closePop = () => {
    dispatch(projectSlice.actions.setFontPopOpened(false));
  }

  const attachFont = (acceptedFiles: File[], fileRejections: FileRejection[], event: DropEvent) => {
    setFile(acceptedFiles.length > 0 ? acceptedFiles[0] : null);
  };

  const uploadFont = () => {
    let ok = true;

    if (fontName.length === 0) {
      alert("Введите название шрифта");
      ok = false;
    }
    if (fontStyle.length === 0) {
      alert("Введите стиль шрифта");
      ok = false;
    }
    if (fontWeight.length === 0) {
      alert("Введите толщину шрифта");
      ok = false;
    }
    if (file === null) {
      alert("Прикрепите файл");
      ok = false;
    }

    if (ok) {
      uploadFontMutation({fontName, fontStyle, fontWeight, fontFiles: file!})
        .then((res) => {
          if ("data" in res) {
            if (res.data.ok) {
              dispatch(projectSlice.actions.setProjectFonts(res.data.fonts!))
            } else {
              alert(res.data.error);
            }
          } else {
            console.log(res.error);
            alert("Неизвестная ошибка. Перезагрузите сервер.")
          }
        })
        .catch(e => {
          console.log(e);
          alert("Неизвестная ошибка. Перезагрузите сервер.")
        })
        .finally(() => {
          formRef.current?.reset();
          setFile(null);
          setFontName("");
          setFontStyle("");
          setFontWeight("");
        });
    }
  };

  const connectFont = (fontName: string, connect: boolean) => {
    connectFontMutation({fontName, pageName: curPage, connect})
      .then(res => {
        if ("data" in res) {
          if (res.data.ok) {
            dispatch(projectSlice.actions.setProjectFonts(res.data.fonts!));
            dispatch(projectSlice.actions.setConnectedFonts(res.data.connectedFonts!));
          } else {
            alert(res.data.error);
          }
        } else {
          alert("Неизвестная ошибка. Перезагрузите сервер.");
          console.log(res.error);
        }
      })
      .catch(err => {
        alert("Неизвестная ошибка. Перезагрузите сервер.");
        console.log(err);
      })
  }

  return (
    <div className='font-pop'>
      <div className='font-pop__back' onClick={closePop}></div>

      <div className='font-pop__content'>
        <form className='font-pop__content-create' ref={formRef}>
          <div className='font-pop__content-create-header'>Шрифты</div>

          <div className='font-pop__content-create-text-item'>
            <label htmlFor='font-pop-font-name-input'>Имя шрифта: </label>
            <input type="text" id="font-pop-font-name-input" placeholder='name' value={fontName} onInput={e => setFontName((e.target as HTMLInputElement).value)}/>
          </div>

          <div className='font-pop__content-create-text-item'>
            <label htmlFor='font-pop-font-style-input'>Стиль шрифта: </label>
            <input type="text" id="font-pop-font-style-input" placeholder='normal' value={fontStyle} onInput={e => setFontStyle((e.target as HTMLInputElement).value)}/>
          </div>

          <div className='font-pop__content-create-text-item'>
            <label htmlFor='font-pop-font-weight-input'>Толщина шрифта: </label>
            <input type="text" id="font-pop-font-weight-input" placeholder='400' value={fontWeight} onInput={e => setFontWeight((e.target as HTMLInputElement).value)}/>
          </div>

          <div className='font-pop__content-create-upload-item'>
            {/* <input type="file" id='font-pop-font-zip-input'/> */}
            <div 
              className='font-pop__content-create-upload-item-text-label' 
              // htmlFor='font-pop-font-zip-input'
            >
              Загрузите архив со шрифтом: 
            </div>

            {/* <label className='font-pop__content-create-upload-item-zone-label' htmlFor='font-pop-font-zip-input'>
              <img src={Icon.ArrowUploadWhite} alt=''/>
              <span>Перетащите сюда файлы </span>
            </label> */}

            <Dropzone accept={{"application/zip": [".zip"]}} multiple={false} onDrop={attachFont} noDragEventsBubbling={true}>
              {
                ({getRootProps, getInputProps, isDragActive}) => (
                  <div {...getRootProps()} className='font-pop__content-create-upload-item-zone-label'>
                    <input {...getInputProps()}/>
                    <img src={Icon.ArrowUploadWhite} alt=''/>
                    {
                      isDragActive ?
                      <span>Да, cюда</span> :
                      <span>
                        {file ? <>{file.name}</> :<>Перетащите сюда архив <br/> или нажмите для выбора</>}
                      </span>
                    }
                  </div>
                )
              }
            </Dropzone>
          </div>

          

          <button type='button' className='font-pop__content-create-btn' onClick={uploadFont}>Создать</button>
        </form>

        <div className='font-pop__content-connect'>
          <div className='font-pop__content-connect-header'>Подключенные шрифты: </div>

          <div className='font-pop__content-connect-list'>
            {fonts.map((font, fontIndex) => (
              <div 
                key={fontIndex} 
                className={'font-pop__content-connect-list-item ' + (connectedFonts.includes(font) ? "font-pop__content-connect-list-item_active" : "")}
                onClick={() => connectFont(font, !connectedFonts.includes(font))}
              >
                <span className='font-pop__content-connect-list-item-square'></span>
                <span className='font-pop__content-connect-list-item-name'>{font}</span>
              </div>
            ))}
          </div>
        </div>
        <button className='font-pop__content-exit' onClick={closePop}>
          <img src={Icon.ExitWhite} alt=''/>
        </button>
      </div>
    </div>
  )
}