import { PageChooser } from "./PagesChooser/PageChooser";
import "./ProjectPage.scss";
import { NodeTree } from "./NodeTree/NodeTree";
import { RenderArea } from "./RenderArea/RenderArea";
import { NodeSettings } from "./NodeSettings/NodeSettings";
import { ToolPanel } from "./ToolPanel/ToolPanel";
import { useAppDispatch, useAppSelector } from "../../../redux/store";
import { useLazyGetTreeQuery } from "../../../redux/api/page.api";
import { TreeDTO } from "../../../dto/TreeDTO";
import { nodeTreeSlice } from "../../../redux/slices/nodeTreeSlice";
import { useEffect } from "react";
import { ProjectRouteEnum, projectSlice } from "../../../redux/slices/projectSlice";
import { useLazyGetStyleSheetQuery } from "../../../redux/api/style.api";
import { StyleSheetDTO } from "../../../dto/StyleSheetDTO";
import { styleTreeSlice } from "../../../redux/slices/styleTreeSlice";
import { StyleTree } from "./StyleTree/StyleTree";
import { StyleComponentSettings } from "./StyleComponentSettings/StyleComponentSettings";
import { FontPop } from "./FontPop/FontPop";
import { useLazyGetConnectedFontsQuery } from "../../../redux/api/font.api";


export function ProjectPage() : JSX.Element {
  const activePage = useAppSelector(state => state.projectSliceReducer.activePage);
  const activeStyleSheet = useAppSelector(state => state.projectSliceReducer.activeStyleSheet);
  const projectRoute = useAppSelector(state => state.projectSliceReducer.projectRoute);
  const [getTree] = useLazyGetTreeQuery();
  const [getSheet] = useLazyGetStyleSheetQuery();
  const [getConnectedFonts] = useLazyGetConnectedFontsQuery();
  const dispatch = useAppDispatch();

  const fontPopOpened = useAppSelector(state => state.projectSliceReducer.fontPopOpened);

  useEffect(() => {
    getTree(activePage)
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            const treeDTO = (res.data.treeDTO as TreeDTO);
            dispatch(nodeTreeSlice.actions.updateTree(treeDTO));
          } else {
            alert("Ты что-то не то делаешь");
          }
        } else {
          alert("Перезагрузи сервер");
        }
      })

    getConnectedFonts(activePage)
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            dispatch(projectSlice.actions.setProjectFonts(res.data.fonts!));
            dispatch(projectSlice.actions.setConnectedFonts(res.data.connectedFonts!))
          } else {
            alert(res.data.error);
          }
        } else {
          alert("Перезагрузи сервер");
        }
      })
  }, [activePage]);

  useEffect(() => {
    getSheet({sheetName: activeStyleSheet})
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            const styleSheetDTO = (res.data.styleSheetDTO as StyleSheetDTO);
            dispatch(styleTreeSlice.actions.updateTree({allStyleComponents: styleSheetDTO.styleComponents}))
          } else {
            alert("Ты что-то не то делаешь");
          }
        } else {
          alert("Перезагрузи сервер");
        }
      });
  }, [activeStyleSheet]);

  return (
    <div className="project-page">
      <ToolPanel/>
      {/* <PageChooser/> */}
      {
        projectRoute === ProjectRouteEnum.MarkupPage ?
        <>
          <NodeTree/>
          <RenderArea/>
          <NodeSettings/>
        </> :
        <>
          <StyleTree/>
          <RenderArea/>
          <StyleComponentSettings/>
        </>
      }
     
      {fontPopOpened && <FontPop/>}
    </div>
  )
}