import { isElementDTO } from "../../../../dto/ElementDTO";
import { isTextDTO } from "../../../../dto/TextDTO";
import { useAppSelector } from "../../../../redux/store";
import { ElementSettings } from "./ElementSettings/ElementSettings";
import "./NodeSettings.scss";
import { TextSettings } from "./TextSettings/TextSettings";

export function NodeSettings(): JSX.Element {
  const pageName = useAppSelector(state => state.projectSliceReducer.activePage);
  const curNode = useAppSelector(state => state.nodeSliceReducer.curNode);

  return (
    <div className="node-settings">
      {curNode !== null && isElementDTO(curNode) && <ElementSettings pageName={pageName} node={curNode}/>}
      {curNode !== null && isTextDTO(curNode) && <TextSettings pageName={pageName} node={curNode}/>}
    </div>
  )
}